import React from "react";
import { Dropdown, NavItem, Button } from "react-bootstrap";
import Usuario, { conUsuario, userNull } from "../Modelos/Usuario";
import { formatoFecha } from "../Utiles/Formatos";

export default class User extends React.Component<{ update: () => void, close: () => void }, Usuario>{
    state = userNull;
    render() {
        const user = this.state;
        return <Dropdown as={NavItem} className='user-menu'>
            <Dropdown.Toggle variant="success" >
                <img src="/logos/logo.svg" className="user-image img-circle elevation-2" alt="Logo2" />
                <span className="d-none d-md-inline">{user.usuario}</span>
            </Dropdown.Toggle>
            <Dropdown.Menu >
                <li className="user-header bg-info">
                    <img src="/logos/logo.svg" className="img-circle elevation-2" alt="Logo" />
                    <p>
                        {user.usuario}
                        <small>{formatoFecha(user.fecha)}</small>
                    </p>
                </li>
                <li className="user-footer">
                    <Button onClick={this.cacheClear.bind(this)} className="btn btn-warning btn-flat">LIMPIAR</Button>
                    <Button onClick={this.salir.bind(this)} className="btn btn-danger btn-flat float-right">SALIR</Button>
                </li>
            </Dropdown.Menu>
        </Dropdown >;
    }
    componentDidMount() {
        conUsuario().get('/api/Auth/me', this.setState.bind(this));
    }
    salir() {
        conUsuario().forceGetWeb('/api/Auth/close', () => {
            localStorage.clear()
            this.setState(userNull)
            this.props.update();
        });
    }
    cacheClear() {
        conUsuario().forceGetWeb('/api/Auth/token', (data) => {
            localStorage.clear()
            localStorage.setItem('Authorization', data.token);
            this.props.update();
        });
    }
}