import React from 'react';
import { Col, Row } from 'react-bootstrap';
import { Route, Link } from "react-router-dom"
import Ventas from './Ventas';
import Region from './Region';
import Factura from './Impreciones/Factura';
import Empresa from './Empresa';
import ListDepartamento from '../Departamento';
import ListTipo from '../Tipos';
import ListUnidad from '../Unidad';
import ListMoneda from '../Moneda';
import Orden from './Impreciones/Orden';
import Compra from './Impreciones/Compra';
import Cotizacion from './Impreciones/Cotizacion';
import Nota from './Impreciones/Nota';
import Reporte from './Impreciones/Reporte';
import Ajuste from './Impreciones/Ajuste';
import Costos from './Costos';
import Utilidades from './Utilidades';
import ListaRoles from '../Rol';
import { Menus, MenuIten } from '../../Utiles/Componentes/Menus';

export default class Config extends React.Component<{ p: string[] }> {
    render() {
        let { p } = this.props;
        return (<Row style={{ minHeight: "calc(100vh - 145px)" }}>
            <Col md={3} style={{ minHeight: "calc(100vh - 145px)" }}>
                <Menus title='CONFIGURACIONES GENERALES'>
                    <Link className='btn btn-default' to='/conf_empresa'>
                        EMPRESA
                    </Link>
                    <Link className='btn btn-default' to='/conf_region'>
                        REGION
                    </Link>
                    <Link className='btn btn-default' to='/conf_ventas'>
                        VENTAS
                    </Link>
                    <MenuIten p={p} to='/moneda' permiso='MONEDA' >
                        MONEDAS
                    </MenuIten>
                    <MenuIten p={p} to='/roles' permiso='USUARIO' >
                        ROLES
                    </MenuIten>
                </Menus>
                <Menus title='CONFIGURACIONES DE IMPRESI√ìN'>
                    <MenuIten p={p} to='/conf_orden' permiso='ORDEN' >
                        ORDEN DE COMPRA
                    </MenuIten>
                    <MenuIten p={p} to='/conf_compra' permiso='COMPRA' >
                        COMPRA
                    </MenuIten>
                    <MenuIten p={p} to='/conf_cotizacion' permiso='COTIZACION' >
                        COTIZACION
                    </MenuIten>
                    <MenuIten p={p} to='/conf_nota' permiso='NOTA' >
                        NOTA DE ENTREGA
                    </MenuIten>
                    <MenuIten p={p} to='/conf_factura' permiso='FACTURA' >
                        FACTURA
                    </MenuIten>
                    <MenuIten p={p} to='/conf_ajustes' permiso='AJUSTE' >
                        AJUSTE DE INVENTARIO
                    </MenuIten>
                    <MenuIten p={p} to='/conf_reportes' permiso='REPORTE' >
                        REPORTES
                    </MenuIten>
                </Menus>
                <Menus title='CONFIGURACIONES DE PRODUCTO'>
                    <MenuIten p={p} to='/departamentos' permiso='DEPARTAMENTO' >
                        DEPARTAMENTOS
                    </MenuIten>
                    <MenuIten p={p} to='/tipo_producto' permiso='TIPO' >
                        TIPO DE PRODUCTO
                    </MenuIten>
                    <MenuIten p={p} to='/unidades' permiso='UNIDAD' >
                        UNIDADES DE MEDIDA
                    </MenuIten>
                    <MenuIten p={p} to='/conf_nota' permiso='NOTA' >
                        NOTA DE ENTREGA
                    </MenuIten>
                    <MenuIten p={p} to='/costos' permiso='PRODUCTO' >
                        MANEJO DE COSTOS
                    </MenuIten>
                    <MenuIten p={p} to='/utilidades' permiso='PRODUCTO' >
                        UTILIDADES DEFAULT
                    </MenuIten>
                </Menus>
            </Col>
            <Col md={9} style={{ minHeight: "calc(100vh - 145px)" }}>
                <Route path="/conf_ventas" component={Ventas} />
                <Route path="/conf_region" component={Region} />
                <Route path="/conf_orden" component={Orden} />
                <Route path="/conf_compra" component={Compra} />
                <Route path="/conf_cotizacion" component={Cotizacion} />
                <Route path="/conf_nota" component={Nota} />
                <Route path="/conf_factura" component={Factura} />
                <Route path="/conf_ajustes" component={Ajuste} />
                <Route path="/conf_reportes" component={Reporte} />
                <Route path="/conf_empresa" component={Empresa} />
                <Route path="/departamentos" component={ListDepartamento} />
                <Route path="/tipo_producto" component={ListTipo} />
                <Route path="/unidades" component={ListUnidad} />
                <Route path="/moneda" component={ListMoneda} />
                <Route path="/costos" component={Costos} />
                <Route path="/utilidades" component={Utilidades} />
                <Route path="/roles" component={ListaRoles} />
            </Col>
        </Row>)
    }
}